// Wait for Vue to be available globally
function initApp() {
  if (!window.Vue) {
    console.warn('Vue not loaded yet, retrying in 100ms...');
    setTimeout(initApp, 100);
    return;
  }

  const { createApp } = window.Vue;
  const app = createApp({
    name: 'DemoApp',
    template: '<div id="app"><chat-widget /></div>'
  });

  // Register the chat widget component
  app.component('chat-widget', window.SophosicChat.Widget);

  // Mount the app
  app.mount('#app');
}

// Start initialization when document is ready
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initApp);
} else {
  initApp();
}
